import React from 'react';
import { connect } from 'react-redux';
import {Table} from 'reactstrap';
import DataTableCell from '../table/routes/basic/Components/DataTableCell';
import { userList } from 'actions/UserList';


class UserList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userList: [],
            showData: false
        }
    }

    componentDidMount(){
        this.props.userList();
    }


    render(){

        return (
            <div className="table-responsive-material">
                <Table>
                    <thead>
                    <tr>
                        <th className="border-top-0"></th>
                        <th className="border-top-0">Last Names</th>
                        <th className="border-top-0">Other Names</th>
                        <th className="border-top-0">Phone Number</th>
                        <th className="border-top-0">Loan Level</th>
                    </tr>
                    </thead>
                    <tbody>

                {this.props.user.userList.map((item) =>  {
                    return (
                        <tr key={item.id}>
                              <td></td>
                              <td>{item.last_name} {item.other_names}</td>
                              <td>{item.email}</td>
                              <td>{item.loan_level}</td>
                              <td>{item.gender}</td>
                        </tr>
                    );
                })}
                    </tbody>
                </Table>
            </div>
            );
    }
}

const mapStateToProps = (state) => ({
    user: state.userList
})



export default connect(mapStateToProps, {userList})(UserList);


